@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');
h1, h2, h3, h4, h5, h6 {
  color: white;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  margin-bottom: 5px;
  margin-left: 10px;
  margin-right: 10px;
  text-decoration: underline;
}
.text-blue {
  color: #002B7F;
}
.text-yellow {
  color: #FCD116;
}
.text-red {
  color: #CE1126;
}
body {
  font-family: "Bungee", Helvetica, sans-serif;
  background-color: rgb(18, 18, 19);
}
* {
  font-family: "Bungee", Helvetica, sans-serif;
}
p {
  color: #ffffff;
  text-align: center;
}
h2 {
  font-size: 1.8rem;
}