.game-button {
    font-size: 1rem;
    padding: 22px 8px 22px 8px;
    margin: 0px;
    border-radius: 10px 10px 10px 10px;
    color: white;
    border: white 1px solid;
    background: #191919;
    cursor:pointer;  
    margin: 5px 0 5px 0;
    background-size: 1px calc(1.1rem*16);
    background: radial-gradient(rgba(23, 23, 23, 1) 0%, rgba(28, 28, 28, 1) 43%, rgba(23, 23, 23, 1) 100%);
    width: calc(1.1rem * 15);
    transition: scale 100ms ease-out;
    text-decoration: none;
    text-align: center;
}
.game-button:active {
    scale: 0.9;
}
.game-button:hover {
    opacity: 0.7;
}
.center-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px; 
    margin-top: 30px;
}

.main-menu {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.menu-content {
    flex-grow: 1;
    display: flex;
    justify-content: center;
}